import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListS1ApplicationComponent } from './list-s1-application.component';

const routes: Routes = [
  {
    path: '',
    component: ListS1ApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ListS1ApplicationRoutingModule { }
