import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UpdateMultipleApplicationComponent } from './update-multiple-application.component';

const routes: Routes = [
  {
    path: '',
    component: UpdateMultipleApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UpdateMultipleApplicationRoutingModule { }
