import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UpdateSpecialApplicationComponent } from './update-special-application.component';

const routes: Routes = [
  {
    path: '',
    component: UpdateSpecialApplicationComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UpdateSpecialApplicationRoutingModule { }
